"use client";

import * as React from "react";
import Link from "next/link";
import { usePathname } from "next/navigation";
import { ShoppingBag, Menu, X } from "lucide-react";
import { useSession, signOut } from "next-auth/react";
import { LoginModal } from "@/components/auth/LoginModal";
import { cn } from "@/lib/utils";
import { useCart } from "@/components/cart/CartProvider";
import { Button } from "@/components/ui/Button";

export function Navigation() {
    const [isMobileMenuOpen, setIsMobileMenuOpen] = React.useState(false);
    const [isLoginOpen, setIsLoginOpen] = React.useState(false);
    const [isUserMenuOpen, setIsUserMenuOpen] = React.useState(false);
    const [scrolled, setScrolled] = React.useState(false);

    const pathname = usePathname();
    const { cartCount, setIsCartOpen } = useCart();
    const { data: session } = useSession();

    // Handle scroll effect
    React.useEffect(() => {
        const handleScroll = () => {
            setScrolled(window.scrollY > 20);
        };
        window.addEventListener("scroll", handleScroll);
        return () => window.removeEventListener("scroll", handleScroll);
    }, []);

    // Close mobile menu on route change
    React.useEffect(() => {
        setIsMobileMenuOpen(false);
        setIsUserMenuOpen(false);
    }, [pathname]);

    // Lock body scroll when mobile menu is open
    React.useEffect(() => {
        if (isMobileMenuOpen) {
            document.body.style.overflow = 'hidden';
        } else {
            document.body.style.overflow = 'unset';
        }
        return () => {
            document.body.style.overflow = 'unset';
        };
    }, [isMobileMenuOpen]);

    const navLinks = [
        { name: "Home", href: "/" },
        { name: "Collection", href: "/products" },
        { name: "About", href: "/about" },
        { name: "Contact", href: "/contact" },
    ];

    const handleSignOut = () => {
        signOut();
        setIsUserMenuOpen(false);
    };

    return (
        <>
            <header
                className={cn(
                    "fixed top-[2.625rem] left-0 right-0 z-50 transition-all duration-500",
                    scrolled
                        ? "bg-white/90 backdrop-blur-xl border-b border-gray-100 py-3 shadow-sm"
                        : "bg-transparent border-b border-transparent py-5"
                )}
            >
                <div className="container-custom mx-auto">
                    <div className="flex items-center justify-between relative">
                        {/* Mobile Menu Button - Left */}
                        <div className="flex-1 md:hidden">
                            <button
                                onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
                                className="p-2 -ml-2 text-black transition-transform active:scale-95"
                                aria-label="Toggle mobile menu"
                            >
                                {isMobileMenuOpen ? (
                                    <X className="w-6 h-6 stroke-[1.5]" />
                                ) : (
                                    <Menu className="w-6 h-6 stroke-[1.5]" />
                                )}
                            </button>
                        </div>

                        {/* Desktop Navigation (Left) */}
                        <nav className="hidden md:flex flex-1 items-center gap-10">
                            {navLinks.map((link) => (
                                <Link
                                    key={link.name}
                                    href={link.href}
                                    className={cn(
                                        "group relative text-[0.6875rem] font-bold tracking-[0.2em] uppercase transition-colors",
                                        pathname === link.href
                                            ? "text-black"
                                            : "text-gray-500 hover:text-black"
                                    )}
                                >
                                    {link.name}
                                    <span
                                        className={cn(
                                            "absolute -bottom-1 left-0 h-px bg-black transition-all duration-300",
                                            pathname === link.href ? "w-full" : "w-0 group-hover:w-full"
                                        )}
                                    />
                                </Link>
                            ))}
                        </nav>

                        {/* Logo (Centered) */}
                        <Link
                            href="/"
                            className="font-display font-black text-2xl md:text-3xl tracking-tighter hover:opacity-70 transition-opacity flex-shrink-0"
                        >
                            intru
                        </Link>

                        {/* Right Side Actions */}
                        <div className="flex flex-1 items-center justify-end gap-1 md:gap-6">
                            {/* Desktop Account/Login */}
                            <div className="hidden md:flex items-center gap-6">
                                {session ? (
                                    <div className="relative">
                                        <button
                                            onClick={() => setIsUserMenuOpen(!isUserMenuOpen)}
                                            className="text-[0.6875rem] font-bold text-gray-500 hover:text-black tracking-[0.2em] uppercase transition-colors"
                                        >
                                            Account
                                        </button>
                                        {isUserMenuOpen && (
                                            <div className="absolute right-0 top-full mt-6 w-56 bg-white border border-gray-100 p-2 z-50 shadow-2xl rounded-sm overflow-hidden animate-slide-in-up">
                                                <div className="px-4 py-3 text-[0.625rem] text-gray-400 font-bold uppercase tracking-widest border-b border-gray-50 mb-1">
                                                    {session.user?.email}
                                                </div>
                                                <Link
                                                    href="/account"
                                                    className="w-full text-left block px-4 py-3 text-xs font-bold uppercase tracking-widest hover:bg-gray-50 transition-colors rounded-sm"
                                                >
                                                    Dashboard
                                                </Link>
                                                <button
                                                    onClick={handleSignOut}
                                                    className="w-full text-left px-4 py-3 text-xs font-bold uppercase tracking-widest text-red-500 hover:bg-red-50 transition-colors rounded-sm"
                                                >
                                                    Sign Out
                                                </button>
                                            </div>
                                        )}
                                    </div>
                                ) : (
                                    <button
                                        onClick={() => setIsLoginOpen(true)}
                                        className="text-[0.6875rem] font-bold text-gray-500 hover:text-black tracking-[0.2em] uppercase transition-colors"
                                    >
                                        Login
                                    </button>
                                )}
                            </div>

                            {/* Cart Button */}
                            <button
                                onClick={() => setIsCartOpen(true)}
                                className="group relative flex items-center gap-1.5 p-2 text-black transition-transform active:scale-95"
                                aria-label="Open shopping cart"
                            >
                                <span className="hidden md:inline text-[0.6875rem] font-bold uppercase tracking-[0.2em] text-gray-500 group-hover:text-black transition-colors">
                                    Bag
                                </span>
                                <div className="relative">
                                    <ShoppingBag className="w-5 h-5 stroke-[1.5]" />
                                    {cartCount > 0 && (
                                        <span className="absolute -top-1.5 -right-1.5 bg-black text-white text-[0.5625rem] font-black w-4 h-4 flex items-center justify-center rounded-full animate-slide-in-up">
                                            {cartCount}
                                        </span>
                                    )}
                                </div>
                            </button>
                        </div>
                    </div>
                </div>
            </header>

            {/* Mobile Menu Overlay */}
            {isMobileMenuOpen && (
                <>
                    <div
                        className="fixed inset-0 bg-black/60 backdrop-blur-sm z-[51] animate-fade-in"
                        onClick={() => setIsMobileMenuOpen(false)}
                    />
                    <div className="fixed top-0 left-0 bottom-0 w-[85%] max-w-[400px] bg-white z-[52] shadow-2xl flex flex-col p-8 animate-slide-in-from-left">
                        <div className="flex justify-between items-center mb-16">
                            <span className="font-display font-black text-2xl tracking-tighter">intru</span>
                            <button
                                onClick={() => setIsMobileMenuOpen(false)}
                                className="p-2 -mr-2"
                                aria-label="Close mobile menu"
                            >
                                <X className="w-8 h-8 stroke-[1]" />
                            </button>
                        </div>
                        <div className="flex flex-col space-y-8">
                            {navLinks.map((link, i) => (
                                <Link
                                    key={link.name}
                                    href={link.href}
                                    className={cn(
                                        "text-4xl font-bold uppercase tracking-tighter transition-opacity",
                                        pathname === link.href && "opacity-50"
                                    )}
                                    style={{
                                        animationDelay: `${0.1 + (i * 0.05)}s`,
                                        animation: 'fadeInUp 0.6s ease-out forwards'
                                    }}
                                    onClick={() => setIsMobileMenuOpen(false)}
                                >
                                    {link.name}
                                </Link>
                            ))}
                        </div>

                        <div className="mt-auto space-y-6">
                            {!session ? (
                                <Button
                                    className="w-full py-6"
                                    onClick={() => {
                                        setIsMobileMenuOpen(false);
                                        setIsLoginOpen(true);
                                    }}
                                >
                                    Sign In
                                </Button>
                            ) : (
                                <Button
                                    variant="outline"
                                    className="w-full py-6"
                                    onClick={handleSignOut}
                                >
                                    Sign Out
                                </Button>
                            )}
                            <div className="flex justify-center gap-8 pt-4">
                                <a
                                    href="https://www.instagram.com/intru.in/"
                                    target="_blank"
                                    rel="noopener noreferrer"
                                    className="text-[0.625rem] font-bold uppercase tracking-widest text-gray-400"
                                >
                                    Instagram
                                </a>
                                <a
                                    href="#"
                                    className="text-[0.625rem] font-bold uppercase tracking-widest text-gray-400"
                                >
                                    WhatsApp
                                </a>
                            </div>
                        </div>
                    </div>
                </>
            )}

            <LoginModal isOpen={isLoginOpen} onClose={() => setIsLoginOpen(false)} />
        </>
    );
}
