import * as React from "react";
import { cn } from "@/lib/utils";

export interface InputProps
    extends React.InputHTMLAttributes<HTMLInputElement> {
    label?: string;
    error?: string;
    leftIcon?: React.ReactNode;
    rightIcon?: React.ReactNode;
    description?: string;
}

const Input = React.forwardRef<HTMLInputElement, InputProps>(
    ({
        className,
        type,
        label,
        error,
        leftIcon,
        rightIcon,
        description,
        id,
        ...props
    }, ref) => {
        const inputId = id || React.useId();

        return (
            <div className="w-full space-y-2">
                {label && (
                    <label
                        htmlFor={inputId}
                        className="text-[0.625rem] font-bold uppercase tracking-[0.2em] text-gray-500"
                    >
                        {label}
                    </label>
                )}
                <div className="relative">
                    {leftIcon && (
                        <div className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-400">
                            {leftIcon}
                        </div>
                    )}
                    <input
                        id={inputId}
                        type={type}
                        className={cn(
                            `flex h-11 w-full border border-gray-100 bg-white px-3 py-2 
                            text-sm font-medium
                            ring-offset-white file:border-0 file:bg-transparent 
                            file:text-sm file:font-medium 
                            placeholder:text-gray-300 placeholder:text-xs placeholder:uppercase placeholder:tracking-wider
                            focus-visible:outline-none focus-visible:ring-2 
                            focus-visible:ring-black focus-visible:ring-offset-2 
                            disabled:cursor-not-allowed disabled:opacity-50 
                            transition-all duration-200
                            rounded-sm`,
                            leftIcon && "pl-10",
                            rightIcon && "pr-10",
                            error && "border-red-500 focus-visible:ring-red-500",
                            className
                        )}
                        ref={ref}
                        {...props}
                    />
                    {rightIcon && (
                        <div className="absolute right-3 top-1/2 -translate-y-1/2 text-gray-400">
                            {rightIcon}
                        </div>
                    )}
                </div>
                {description && !error && (
                    <p className="text-xs text-gray-400 font-medium">
                        {description}
                    </p>
                )}
                {error && (
                    <p className="text-xs font-bold text-red-500 uppercase tracking-wider animate-slide-in-up">
                        {error}
                    </p>
                )}
            </div>
        );
    }
);

Input.displayName = "Input";

export { Input };
